<template>
  <md-card>
    <md-card-header>
      <div class="md-title">Github üzerinden:</div>
    </md-card-header>

    <md-card-content v-html="gitProfile"> </md-card-content>
  </md-card>
</template>

<script>
import axios from "axios";
import { marked } from "marked";
export default {
  name: "GithubProfileReadMe",
  props: {
    username: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      gitProfile: "",
    };
  },
  created() {
    this.getGitProfText();
  },
  methods: {
    getGitProfText() {
      axios
        .get(
          `https://raw.githubusercontent.com/${this.username}/${this.username}/master/README.md`
        )
        .then((response) => {
          this.gitProfile = marked.parse(response.data);
        });
    },
  },
};
</script>

